<template>
  <div id="MathMachine" class="pb-16">
    
    <Alert ref="Alert"/>

    
    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :score="score" :maxW="'max-w-5xl'" ref="InteractiveBord"/>


    <div class="w-full mt-10 px-5 flex flex-col items-center font-bold">
      <div class="bg-gray-500 w-1/4 h-12 sm:h-16 md:h-24 rounded-lg">
          <span class="text-4xl sm:text-5xl md:text-6xl " v-text="question" />
      </div>
      <div class="w-full flex flex-wrap max-w-5xl mt-3">
        <div class="md:p-1 w-1/6 h-12 sm:h-16 md:h-20" v-for="(number, index) in numbers" :key="index">

          <div class="h-full w-full rounded-lg overflow-hidden"
            :class="{'bg-green-500':number.isClick && !number.hidden,'bg-gray-700':!number.isClick && !number.hidden,
                    'cursor-pointer border':!number.hidden}"
            :style="number.hidden?'background-color: #0000;':''"
            @click="clickNumber(number)">
            <span class="text-3xl sm:text-4xl md:text-5xl" v-text="number.hidden ? '':number.num" />
          </div>

        </div>
      </div>
    </div>
    
    
  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
export default {
  name: 'MathMachine',
  components: {
    Alert,
    InteractiveBord
  },
  data() {
    return {
      question:null,
      answer:null,

      numbers:[],

      userNumbers:[],

      score:0,

      stopGame:false,

    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.addNumbers();
      this.getQuestion();
    },
    addNumbers(){
      for (let i = 0; i < 54; i++) {
        this.numbers.push({
          id:i,
          num:Math.floor(Math.random() * 9)+1,
          isClick:false,
          hidden:false
        })
      }
    },
    clickNumber(num){
      var vm=this;
      //if(vm.stopGame)return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();
      if(vm.userNumbers.length > 10) return;

      if(num.isClick){
        num.isClick = false;
        let index = this.userNumbers.indexOf(num);
        if(index !== -1){
            this.userNumbers.splice(index,1);
        }
        return;
      }
      if(num.hidden) return;

      vm.userNumbers.push(num)
      num.isClick = true;

      vm.check();
    },
    check(){
      var vm=this;
      var alert = vm.$refs.Alert;
      var sum = 0;
      vm.userNumbers.forEach(num => {
        sum += num.num;
      });

      if(sum == vm.question){

        vm.score += 2;
        alert.alertShow("+2")
        console.log("yes")
        vm.userNumbers.forEach(num => {
          num.hidden = true
        });
        vm.userNumbers = []
        vm.getQuestion();

      }else if(sum > vm.question){
        
        vm.score -= 1;
        alert.alertShow("-1","bg-red-500")
        console.log("out")
        vm.userNumbers.forEach(num => {
          num.isClick = false
        });
        vm.userNumbers = []

      }
    },
    getQuestion(){
      var vm=this;

      var num = 0;
      var numbers=vm.reOrderArray(vm.numberWithOuthidden());
      var rand;

      if(numbers.length > 5){
        rand = 5
      }else if (numbers.length > 2) {
        rand = 2
      }else if (numbers.length > 0) {
        rand = 1;
      }else {
        vm.done();
        return
      }



      for (let i = 0; i < rand; i++) {
        num += numbers[i].num
      }

      vm.question = num
    },
    reOrderArray(array){
      var a = array;
      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      return a;
    },
    numberWithOuthidden(){
      return this.numbers.filter(num => {
          return !num.hidden;
      });
    },
    done(){
      this.question = "DONE"
      console.log("Done")
    },




    updateTimeOver(){
      this.stopGame = true;
      this.$refs.Alert.alertTimeOver();
    },

    changeSettings(){
      var vm = this;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.MStart();
    }
  },
}
</script>

